<template>
  <li class="catalog__item">
    <router-link class="catalog__pic" :to="{ name: 'product', params: { id: product.id } }">
      <img :src="product.image" :alt="product.title" />
    </router-link>

    <h3 class="catalog__title">
      <a href="#">
        {{ product.title }}
      </a>
    </h3>

    <span class="catalog__price"> {{ product.price | numbersFormat }} ₽ </span>

    <ul class="colors colors--black">
      <li class="colors__item" v-for="color in productColors" :key="color.id">
        <label class="colors__label">
          <input class="colors__radio sr-only" type="radio" :value="color.id"/>
          <span class="colors__value" :style="{ backgroundColor: color }"> </span>
        </label>
      </li>
    </ul>
  </li>
</template>

<script>
import gotoPage from "@/helpers/gotoPage";
import numbersFormat from "@/helpers/numbersFormat";

export default {
  props: ["product"],
  filters: {
    numbersFormat,
  },
  computed: {
    productColors() {
      const productColors = this.product.colors;
      return productColors.map((color) => color.code);
    },
  },
  methods: {
    gotoPage,
  },
};
</script>
